import React, { useEffect, useState, useLayoutEffect, Fragment } from "react";
// redux components
import { connect } from "react-redux";
import {
  autoLogoutRequest,
  userLoadRequest,
  resetLoadUser,
} from "../../Redux/Actions/UserActions/userActions";
import { updateUserRequest } from "../../Redux/Actions/AdminActions/updateUserActions";

// core components
import GridItem from "../Core/GridItem.js";
import GridContainer from "../Core/GridContainer.js";
import Card from "../Core/Card.js";
import CardHeader from "../Core/CardHeader.js";
import CardBody from "../Core/CardBody.js";
// styling components
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import styles from "../Styles/userPageStyle.js";
import Lottie from "react-lottie";
import * as trailLoading from "../../assets/gifs/trailLoading.json";
import { SketchPicker } from "react-color";
// @material-ui/core
import Button from "../Core/Button.js";
import { withStyles, TextField, makeStyles } from "@material-ui/core";

const hexToRgb = (input) => {
  input = input + "";
  input = input.replace("#", "");
  let hexRegex = /[0-9A-Fa-f]/g;
  if (!hexRegex.test(input) || (input.length !== 3 && input.length !== 6)) {
    throw new Error("input is not a valid hex color.");
  }
  if (input.length === 3) {
    let first = input[0];
    let second = input[1];
    let last = input[2];
    input = first + first + second + second + last + last;
  }
  input = input.toUpperCase(input);
  let first = input[0] + input[1];
  let second = input[2] + input[3];
  let last = input[4] + input[5];
  return (
    parseInt(first, 16) +
    ", " +
    parseInt(second, 16) +
    ", " +
    parseInt(last, 16)
  );
};

const useStyles = makeStyles(styles);
const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: trailLoading.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

function ViewManagement(props) {
  const { lex, user, session, loadedUser, autoDisconnect } = props;
  const classes = useStyles();
  const [backgroundColor, setBackgroundColor] = useState({
    r: 32,
    g: 14,
    b: 14,
    a: 0.51,
  });
  const [fontColor, setFontColor] = useState({
    r: 32,
    g: 14,
    b: 14,
    a: 0.51,
  });

  const [logoEngSize, setLogoEngSize] = useState(250);
  const [logoEngVer, setLogoEngVer] = useState(-35);
  const [logoEngHorz, setLogoEngHorz] = useState(-80);
  const [logoHebSize, setLogoHebSize] = useState(250);
  const [logoHebVer, setLogoHebVer] = useState(-35);
  const [logoHebHorz, setLogoHebHorz] = useState(0);

  useLayoutEffect(() => {
    if (autoDisconnect) {
      props.autoLogoutRequest(lex);
      props.history.push("/loginPage");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [autoDisconnect]);

  useEffect(() => {
    if (
      document.cookie.match("connect.sid") &&
      document.cookie.match("connect.sid").input === session
    ) {
      props.userLoadRequest();
      return () => {
        props.resetLoadUser();
      };
    } else {
      props.autoLogoutRequest(lex);
      props.history.push("/loginPage");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    setLogoEngSize(user.logoEngSize);
    setLogoEngVer(user.logoEngVer);
    setLogoEngHorz(user.logoEngHorz);
    setLogoHebSize(user.logoHebSize);
    setLogoHebVer(user.logoHebVer);
    setLogoHebHorz(user.logoHebHorz);
    setBackgroundColor(user.backgroundColor);
    setFontColor(user.fontColor);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loadedUser]);

  const handleChange = (color) => {
    setBackgroundColor(color.rgb);
  };

  const handleFontChange = (color) => {
    setFontColor(color.rgb);
  };

  const handleSaveChanges = () => {
    const tmpUser = {
      tmpid: user._id,
      tmpname: user.name,
      tmpemail: user.email,
      tmpphone: user.phone,
      tmppassword: "",
      tmpnewPassword: "",
      tmpstoreName: user.storeName,
      tmpaddress: user.address,
      tmplicenceNumber: user.licenceNumber,
      tmpcurrencyPrecision: user.currencyPrecision,
      tmpcalculatorPrecision: user.calculatorPrecision,
      tmpuserLogo: user.userLogo,
      tmpUserLanguage: user.userLanguage,
      tmpLogoEngSize: logoEngSize,
      tmpLogoEngVer: logoEngVer,
      tmpLogoEngHorz: logoEngHorz,
      tmpLogoHebSize: logoHebSize,
      tmpLogoHebVer: logoHebVer,
      tmpLogoHebHorz: logoHebHorz,
      tmpbackgroundColor: backgroundColor,
      tmpfontColor: fontColor,
    };
    console.log(tmpUser);
    props.updateUserRequest(tmpUser);
    return;
  };

  return (
    <Fragment>
      <ToastContainer />
      {loadedUser ? (
        <Fragment>
          <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
              <Card>
                <CardHeader
                  style={{
                    background: `rgba(
                      ${backgroundColor.r},
                      ${backgroundColor.g},
                      ${backgroundColor.b},
                      ${backgroundColor.a}
                    )`,
                    boxShadow:
                      "0 4px 20px 0 rgba(" +
                      hexToRgb("#000") +
                      ",.14), 0 7px 10px -5px rgba(" +
                      hexToRgb("#9c27b0") +
                      ",.4)",
                    paddingTop: "2px",
                  }}
                >
                  <GridContainer>
                    {user.userLanguage === "english" ? (
                      <Fragment>
                        <GridItem xs={12} sm={5} md={5} lg={5}>
                          <h1
                            className={classes.cardTitleBrown}
                            style={{
                              color: `rgba(
                                ${fontColor.r},
                                ${fontColor.g},
                                ${fontColor.b},
                                ${fontColor.a}
                              )`,
                            }}
                          >
                            Currencies Table
                          </h1>
                          <h4
                            className={classes.cardTitleBrown}
                            style={{
                              color: `rgba(
                                ${fontColor.r},
                                ${fontColor.g},
                                ${fontColor.b},
                                ${fontColor.a}
                              )`,
                            }}
                          >
                            no commission
                          </h4>
                        </GridItem>
                        <GridItem xs={12} sm={2} md={2} lg={2}>
                          <div className={classes.logoImage}>
                            {user.userLogo !== "" && (
                              <img
                                src={user.userLogo}
                                alt="logo"
                                style={{
                                  width: `${logoEngSize}px`,
                                  top: `${logoEngVer}px`,
                                  marginLeft: `${logoEngHorz}px`,
                                }}
                                className={classes.currenciesImg}
                              />
                            )}
                          </div>
                        </GridItem>
                        <GridItem xs={12} sm={5} md={5} lg={5}>
                          <h3
                            className={classes.cardTitleBrown}
                            style={{
                              color: `rgba(
                                ${fontColor.r},
                                ${fontColor.g},
                                ${fontColor.b},
                                ${fontColor.a}
                              )`,
                              textAlign: "right",
                              marginTop: "55px",
                              marginRight: "30px",
                            }}
                          >
                            last update : 1/1/2020 00:00:00
                          </h3>
                        </GridItem>
                      </Fragment>
                    ) : (
                      <Fragment>
                        <GridItem xs={12} sm={12} md={5} lg={5}>
                          <h3
                            className={classes.cardTitleBrown}
                            style={{
                              textAlign: "left",
                              marginTop: "55px",
                              marginLeft: "30px",
                              color: `rgba(
                                ${fontColor.r},
                                ${fontColor.g},
                                ${fontColor.b},
                                ${fontColor.a}
                              )`,
                            }}
                          >
                              : 1/1/2020 00:00:00
                          </h3>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={2} lg={2}>
                          <div className={classes.logoImage}>
                            <img
                              src={user.userLogo}
                              alt="logo"
                              style={{
                                width: logoHebSize.toString() + "px",
                                top: logoHebVer.toString() + "px",
                                marginLeft: logoHebHorz.toString() + "px",
                              }}
                              className={classes.currenciesImg}
                            />
                          </div>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={5} lg={5}>
                          <h1
                            className={classes.cardTitleBrown}
                            style={{
                              color: `rgba(
                                ${fontColor.r},
                                ${fontColor.g},
                                ${fontColor.b},
                                ${fontColor.a}
                              )`,
                              padding: "0px",
                              textAlign: "right",
                              fontSize: "8vh",
                            }}
                          >
                             "
                          </h1>
                          <h4
                            className={classes.cardTitleBrown}
                            style={{
                              color: `rgba(
                                ${fontColor.r},
                                ${fontColor.g},
                                ${fontColor.b},
                                ${fontColor.a}
                              )`,
                              textAlign: "right",
                              fontSize: "3vh",
                            }}
                          >
                              | no commission
                          </h4>
                        </GridItem>
                      </Fragment>
                    )}
                  </GridContainer>
                </CardHeader>
                <CardBody>
                  {user.userLanguage === "english" ? (
                    <GridContainer>
                      <GridItem xs={12} sm={12} md={4}>
                        <br />
                        <Button fullWidth color="primary" fontSize="14px" round>
                          logo measurements
                        </Button>
                        <br />
                        <br />
                        <Button fullWidth color="warning" round>
                          {"size : " + logoEngSize}
                        </Button>
                        <TextField
                          type="number"
                          autoFocus
                          label="set size"
                          value={logoEngSize}
                          onChange={(e) => setLogoEngSize(e.target.value)}
                          fullWidth
                        />
                        <Button fullWidth color="warning" round>
                          {"vertical: " + logoEngVer}
                        </Button>
                        <TextField
                          type="number"
                          autoFocus
                          label="set vertical"
                          value={logoEngVer}
                          onChange={(e) => setLogoEngVer(e.target.value)}
                          fullWidth
                        />
                        <Button fullWidth color="warning" round>
                          {"horizontal: " + logoEngHorz}
                        </Button>
                        <TextField
                          type="number"
                          autoFocus
                          label="set horizontal"
                          value={logoEngHorz}
                          onChange={(e) => setLogoEngHorz(e.target.value)}
                          fullWidth
                        />
                      </GridItem>
                      <GridItem xs={12} sm={12} md={4}>
                        <br />
                        <Button
                          fullWidth
                          fontSize="14px"
                          round
                          style={{
                            background: `rgba(
                              ${backgroundColor.r},
                              ${backgroundColor.g},
                              ${backgroundColor.b},
                              ${backgroundColor.a}
                            )`,
                          }}
                        >
                          choose background color
                        </Button>
                        <br />
                        <br />
                        <div
                          style={{
                            marginLeft: "50px",
                          }}
                        >
                          <SketchPicker
                            color={backgroundColor}
                            onChange={handleChange}
                            style={{
                              marginLeft: "50px",
                            }}
                          />
                        </div>
                      </GridItem>
                      <GridItem xs={12} sm={12} md={4}>
                        <br />
                        <Button
                          fullWidth
                          fontSize="14px"
                          round
                          style={{
                            background: `rgba(
                              ${fontColor.r},
                              ${fontColor.g},
                              ${fontColor.b},
                              ${fontColor.a}
                            )`,
                          }}
                        >
                          choose font color
                        </Button>
                        <br />
                        <br />
                        <div
                          style={{
                            marginLeft: "50px",
                          }}
                        >
                          <SketchPicker
                            color={fontColor}
                            onChange={handleFontChange}
                          />
                        </div>
                      </GridItem>
                    </GridContainer>
                  ) : (
                    <GridContainer>
                      <GridItem xs={12} sm={12} md={4}>
                        <br />
                        <Button
                          fullWidth
                          fontSize="14px"
                          round
                          style={{
                            background: `rgba(
                              ${backgroundColor.r},
                              ${backgroundColor.g},
                              ${backgroundColor.b},
                              ${backgroundColor.a}
                            )`,
                          }}
                        >
                           
                        </Button>
                        <br />
                        <br />
                        <div
                          style={{
                            marginLeft: "50px",
                          }}
                        >
                          <SketchPicker
                            color={backgroundColor}
                            onChange={handleChange}
                          />
                        </div>
                      </GridItem>
                      <GridItem xs={12} sm={12} md={4}>
                        <br />
                        <Button
                          fullWidth
                          fontSize="14px"
                          round
                          style={{
                            background: `rgba(
                              ${fontColor.r},
                              ${fontColor.g},
                              ${fontColor.b},
                              ${fontColor.a}
                            )`,
                          }}
                        >
                           
                        </Button>
                        <br />
                        <br />
                        <div
                          style={{
                            marginLeft: "50px",
                          }}
                        >
                          <SketchPicker
                            color={fontColor}
                            onChange={handleFontChange}
                          />
                        </div>
                      </GridItem>
                      <GridItem xs={12} sm={12} md={4}>
                        <br />
                        <Button fullWidth color="primary" fontSize="14px" round>
                            
                        </Button>
                        <br />
                        <br />
                        <Button fullWidth color="warning" round>
                          {logoHebSize + " : "}
                        </Button>
                        <TextField
                          type="number"
                          autoFocus
                          label=""
                          value={logoHebSize}
                          onChange={(e) => setLogoHebSize(e.target.value)}
                          fullWidth
                        />
                        <Button fullWidth color="warning" round>
                          {logoHebVer + " :  "}
                        </Button>
                        <TextField
                          type="number"
                          autoFocus
                          label=" "
                          value={logoHebVer}
                          onChange={(e) => setLogoHebVer(e.target.value)}
                          fullWidth
                        />
                        <Button fullWidth color="warning" round>
                          {logoHebHorz + " :  "}
                        </Button>
                        <TextField
                          type="number"
                          autoFocus
                          label=" "
                          value={logoHebHorz}
                          onChange={(e) => setLogoHebHorz(e.target.value)}
                          fullWidth
                        />
                      </GridItem>
                    </GridContainer>
                  )}
                </CardBody>
                <Button
                  fullWidth
                  color="success"
                  fontSize="14px"
                  round
                  onClick={handleSaveChanges}
                >
                  {user.userLanguage === "english"
                    ? "save changes"
                    : " "}
                </Button>
              </Card>
            </GridItem>
          </GridContainer>
        </Fragment>
      ) : (
        <Lottie options={defaultOptions} height={200} width={200}>
          LOADING
        </Lottie>
      )}
    </Fragment>
  );
}

const mapDispatchToProps = (dispatch) => ({
  autoLogoutRequest: (email) => dispatch(autoLogoutRequest(email)),
  userLoadRequest: () => dispatch(userLoadRequest()),
  updateUserRequest: (user) => dispatch(updateUserRequest(user)),
  resetLoadUser: () => dispatch(resetLoadUser()),
});

const mapStateToProps = (state) => {
  return {
    session: state.userReducer.session,
    lex: state.userReducer.lex,
    loadedUser: state.userReducer.loadedUser,
    autoDisconnect: state.userReducer.autoDisconnect,
    user: state.userReducer.user,
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(ViewManagement));
